import React, { FunctionComponent } from 'react'
import styled from 'styled-components'
import { Image } from './image'

const Outer = styled.div`
  position: relative;
`

const ImageContainer = styled.div`
  img {
    width: 100%;
    height: auto;
  }
`

const InfoContainer = styled.div`
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 1.25rem 1.75rem;
  box-sizing: border-box;
  opacity: 0;
  transition: all 0.6s;
  ${Outer}:hover & {
    opacity: 1;
    backdrop-filter: blur(1rem);
  }
`

const Name = styled.div`
  font-size: 0.8125rem;
  display: flex;
  flex-direction: column;
  filter: blur(0.25rem);
  &::after {
    content: '';
    margin: 0.5rem 0;
    height: 0.125rem;
    background-color: var(--navy);
    display: block;
    align-self: stretch;
  }
  ${Outer}:hover & {
    filter: none;
  }
`

const Type = styled.div`
  font-size: 0.75rem;
  filter: blur(0.25rem);
  ${Outer}:hover & {
    filter: none;
  }
`

type WorkProps = {
  name: string,
  type: string,
  imageName: string
}
export const Work: FunctionComponent<WorkProps> = ({name, type, imageName}) => {
  return <Outer>
    <ImageContainer>
      <Image name={imageName} width="1200" height="849" />
    </ImageContainer>
    <InfoContainer>
      <Name>
        {name}
      </Name>
      <Type>
        {type}
      </Type>
    </InfoContainer>
  </Outer>
}